*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

input{
  color: peru;
  width: 100%;
  font-size: 1.2rem;
}


.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 3px solid plum;
  border-radius: 30px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;

}


.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 200px;
}